.transactions_popup {
    display: flex;
    margin: auto;
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 2;
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s linear;
}
.transactions_popup_opened {
    visibility: visible;
    opacity: 1;
}